.btn, .btn-outline-primary, .btn-sm {
  border-radius: 25px;
  color: #c3ff37;
  background-color: #244975;
  outline: none;
  border: none;
  font-size: 13px;
}

.btn:hover, .btn-outline-primary:hover, .btn-sm:hover {
  color: #244975;
  background-color: #c3ff37;
  outline: none;
  border: none;
  font-size: 13px;
}

.btn:focus, .btn-outline-primary:focus, .btn-sm:focus {
  color: #c3ff37;
  background-color: #244975;
  outline: none;
  border: none;
  box-shadow: 0 0 0 2px rgba(195, 255, 55, 0.9);
}

.form-control, .form-control-sm {
  border-radius: 25px;
  border: none;
  font-size: 13px;
  flex-direction: column;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.375rem 0.75rem;
}

.form-control:focus, .form-control-sm:focus {
  border-radius: 25px;
  box-shadow: 0 0 0 2px rgba(195, 255, 55, 0.705);
  font-size: 13px;
}

.table {
  background-color: #244975;
  border-radius: 10px;
  box-shadow: 0 0 0 2px rgba(195, 255, 55, 0.705);
  color: white;
  border-color: transparent;
}

